import { Navbar } from '@/components/navbar'
import { getTopAnime } from '@/lib/api'
import { AnimeCard } from '@/components/anime-card'
import { FunSkeletonGrid } from '@/components/skeleton-loader'
import { Suspense } from 'react'

async function TrendingContent() {
  const data = await getTopAnime(1)
  return <>{data.data.length > 0 ? (
    <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-4 md:gap-6">
      {data.data.map(anime => (
        <AnimeCard key={anime.mal_id} anime={anime} />
      ))}
    </div>
  ) : (
    <p className="text-center text-muted-foreground">No trending anime found</p>
  )}</>
}

export default function TrendingPage() {
  return (
    <main className="min-h-screen bg-background">
      <Navbar />

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        <h1 className="text-4xl font-bold mb-2 text-foreground">Trending Anime</h1>
        <p className="text-muted-foreground mb-8">
          Discover what's trending in the anime community
        </p>
      </div>

      <Suspense fallback={<FunSkeletonGrid />}>
        <TrendingContent />
      </Suspense>
    </main>
  )
}
